/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
.container {
  padding: 0 1.5rem;
  max-width: 80rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}

.flex-reversed {
  flex-direction: row-reverse;
}

:root {
  --header-height: 5rem;
  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --font-body: "Poppins", sans-serif;
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;
  /*========== Letter Spacing ==========*/
  --spacing-big: 0.02em;
  --spacing-small: 0.01em;
  /*========== Margenes ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --m-0-5: 0.5rem;
  --m-0-75: 0.75rem;
  --m-1: 1rem;
  --m-1-5: 1.5rem;
  --m-2: 2rem;
  --m-2-5: 2.5rem;
  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}
@media screen and (max-width: 1149px) {
  :root {
    --big-font-size: 2rem;
    --h1-font-size: 1.8rem;
    --h2-font-size: 1rem;
    --h3-font-size: 0.5rem;
    --normal-font-size: 0.8rem;
    --small-font-size: 0.4rem;
    --smaller-font-size: 0.3rem;
  }
}
@media screen and (min-width: 968px) {
  :root {
    --big-font-size: 3rem;
    --h1-font-size: 2.5rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.813rem;
  }
}
@media screen and (max-width: 768px) {
  :root {
    --big-font-size: 2rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1rem;
    --h3-font-size: 0.5rem;
    --normal-font-size: 0.8rem;
    --small-font-size: 0.4rem;
    --smaller-font-size: 0.3rem;
  }
}

:root {
  /*========= Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --hue: 152;
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-blue: hsla(0, 0%, 9%, 1);
  --primary-color: hsla(221, 99%, 59%, 1);
  --primary-color-light: hsla(221, 99%, 59%, 0.1);
  --secondary-color: hsla(224, 100%, 9%, 1);
  --secondary-color-light: hsla(224, 100%, 9%, 0.1);
  --tertiary-color: hsla(93, 98%, 47%, 1);
  --tertiary-color-light: hsla(93, 98%, 47%, 0.2);
  --bg-light: hsla(151, 100%, 50%, 0.08);
  --title-color: var(--dark-blue);
  --text-color: var(--secondary-color);
  --button-secondary-color: var(--secondary-color);
  --button-primary-color: var(--primary-color);
  --bg-dark: var(--black);
  --bg-default: var(--primary-color);
}

.bg-opacity-blue {
  background: var(--primary-color-light);
}
.bg-opacity-green {
  background: var(--tertiary-color-light);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  font-family: var(--font-body);
  font-weight: 400;
  overflow-x: hidden;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.hero__title {
  font-size: var(--big-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--m-2);
  line-height: 4.0625rem;
}
.hero__description {
  font-size: var(--normal-font-size);
  margin-bottom: var(--m-1);
  line-height: 1.875rem;
}
@media (max-width: 48em) {
  .hero__title {
    line-height: 3.125rem;
  }
  .hero__description {
    line-height: 2.5rem;
    width: 80vw;
  }
}

.about__title {
  font-size: var(--h1-font-size);
  font-weight: var(--font-medium);
  line-height: 3.1875rem;
  letter-spacing: var(--spacing-big);
}
.about__card-description {
  font-size: var(--normal-font-size);
  line-height: 1.6875rem;
  letter-spacing: var(--spacing-small);
  margin: 1.5rem 0;
}

.features__details-title {
  font-size: var(--h3-font-size);
  font-weight: var(--font-bold);
}
@media (max-width: 48em) {
  .features__details-title {
    font-size: var(--h1-font-size);
  }
}
.features__details-description {
  font-size: var(--normal-font-size);
  line-height: 1.6875rem;
  letter-spacing: var(--spacing-small);
  margin: 1.5rem 0;
  color: var(--text-color);
}

.footer {
  color: var(--white);
  line-height: 1.125rem;
  letter-spacing: var(--spacing-big);
}
.footer__title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-bold);
}
.footer__information, .footer__copy {
  font-size: var(--normal-font-size);
}

/*============== ANIMATION ===============*/
.animate {
  animation: fadeIn 0.3s ease-in forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/*=============== BUTTON ===============*/
.button {
  display: inline-flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  border: none;
  cursor: pointer;
  background: transparent;
}
.button--outline {
  color: var(--bg-default) !important;
  border: solid 1px currentColor;
  padding: 0.75rem 1.5rem;
}
.button--outline:hover {
  color: var(--text-color);
}

/*=============== HEADER ===============*/
.header {
  width: 100%;
  overflow: hidden;
}

/*=============== NAV ===============*/
.nav {
  width: 100vw;
  height: var(--header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__logo {
  width: 12.5rem;
}
.nav__hamburger {
  display: none;
  font-size: 1.5rem;
  text-align: center;
  line-height: 1em;
  padding: 0.5rem;
}
.nav__hamburger:hover, .nav__hamburger:active {
  background-color: var(--bg-dark);
  color: var(--white);
}
.nav__hamburger--close {
  transform: translateX(-1.25rem);
  transition: all 0.3s ease-in;
  opacity: 0;
}
.nav__list {
  list-style: none;
  display: flex;
  align-items: center;
  gap: var(--m-1-5);
}
.nav__item {
  text-decoration: none;
}
.nav__link {
  color: var(--text-color);
  font-weight: var(--font-medium);
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.03em;
  transition: 0.4s;
}
.nav__link:hover {
  color: var(--primary-color);
}
.nav__link-icon {
  font-size: 1rem;
  margin-right: var(--m-0-5);
}
@media (max-width: 48em) {
  .nav {
    position: fixed;
    top: 0;
    background: var(--white);
    z-index: 100;
  }
  .nav__logo {
    width: 12.5rem;
  }
  .nav__hamburger {
    display: grid;
  }
  .nav__hamburger--close {
    transform: translateX(0);
    opacity: 1;
  }
  .nav__list {
    flex-direction: column;
    position: absolute;
    top: var(--header-height);
    left: 0;
    right: 0;
    background: var(--white);
    height: 70vh;
    padding: 1rem;
    opacity: 0;
  }
}

.scroll-header {
  position: fixed;
  top: 0;
  background: var(--white);
  z-index: 100;
  animation: fadeIn 0.3s ease-in;
}

.show-menu {
  opacity: 1;
}

/*============== HERO ===============*/
.hero {
  background: var(--bg-default);
  width: 100%;
  color: var(--white);
  clip-path: polygon(0 0, 100% 0%, 100% 90%, 0% 100%);
  display: grid;
  grid-template-columns: repeat(11, minmax(min-content, 1fr));
  grid-template-rows: repeat(4, 1fr);
  margin: 0 auto;
  height: 40rem;
}
.hero__container {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-column: 4/span 5;
  grid-row: 1/-1;
  justify-self: center;
}
.hero__content {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero__button {
  color: var(--white);
  background: var(--bg-dark);
  padding: 1rem 1.5625rem;
  margin-top: 2rem;
}
.hero__image {
  mix-blend-mode: luminosity;
  grid-row: 1/-1;
  align-self: flex-end;
  justify-self: center;
}
.hero__image--right {
  grid-column: 8/-1;
  transform: translateY(-3.125rem);
}
.hero__image--left {
  grid-column: 1/span 4;
  justify-self: left;
}
@media (max-width: 71.8125em) {
  .hero {
    height: 45rem;
  }
}
@media (max-width: 48em) {
  .hero {
    margin-top: var(--header-height);
  }
}

/*============== ABOUT ===============*/
.about {
  background: var(--bg-light);
  display: grid;
  grid-template-columns: repeat(11, minmax(min-content, 1fr));
  grid-template-rows: repeat(4, 1fr);
  justify-content: center;
  align-items: center;
  margin-top: -3.75rem;
  padding: 2rem;
  height: 37.5rem;
  opacity: 0;
}
.about__container {
  text-align: center;
  _padding: 1.5rem;
  grid-column: 3/span 7;
  grid-row: 1/-1;
  justify-self: center;
}
.about__content {
  display: flex;
  gap: var(--m-1-5);
  align-items: center;
  justify-content: center;
  margin-top: var(--m-2-5);
}
.about__card {
  width: 15.8125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.about__card-image {
  width: 6.25rem;
  height: 6.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50vh;
}
.about__card-image img {
  width: 3rem;
  height: 3rem;
}
.about__image {
  grid-row: 1/-1;
  justify-self: center;
}
.about__image--right {
  grid-column: 10/-1;
  transform: translateY(-6.25rem);
}
.about__image--left {
  grid-column: 1/span 3;
  align-self: end;
  transform: translate(-50%, -50%);
  _margin: 3.125rem 0;
}
@media (max-width: 71.8125em) {
  .about {
    height: 57.5rem;
  }
  .about__content {
    flex-wrap: wrap;
  }
}

/*============== FEATURES ===============*/
.features {
  margin: 1.5rem 0;
}
.features__container {
  _margin: 1.5rem 0;
}
.features__content {
  padding: 1.5rem 0;
  width: 90%;
  display: grid;
  place-content: center;
  margin: auto;
}
.features__section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  _flex-wrap: wrap;
  gap: 1.5rem;
  padding: 1.5rem 0;
  margin: 3rem 0;
  opacity: 0;
}
.features__details {
  width: 50%;
}
.features__details-button {
  color: var(--button-primary-color);
}
@media (max-width: 48em) {
  .features__section {
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
    padding: 1.5rem 0;
    margin: 3rem 0;
  }
  .features__details {
    width: 90%;
  }
}

/*=============== FOOTER ===============*/
.footer {
  background: var(--bg-dark);
  width: 100%;
  padding: 4rem 2rem;
  opacity: 0;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.footer__data {
  margin: 1.5rem 0;
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}
.footer__note {
  margin-top: 3rem;
}