html {
	font-size: 100%;
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

body {
	width: 100%;
	font-family: var(--font-body);
	font-weight: 400;
	overflow-x: hidden;
}

ul {
	list-style: none;
}

img {
	max-width: 100%;
	height: auto;
}
